import React from 'react';
import constants from '../../constants/help';
import { CategoryCardWrapper } from '../shared/components';
import { Link } from 'gatsby';
export const CategoriesSection = () => {

  const CategoryCard = ({ fadedTitle, title, icon, link }) => (
    <div className='m-3 text-center d-flex justify-content-center'>
      <Link to={link}>
        <CategoryCardWrapper>
          <img src={icon} className='img-fluid' alt={title + ' иконка'} />
          <h5><span className='faded-text'>{fadedTitle}</span></h5>
          <h3>{title}</h3>
        </CategoryCardWrapper>
      </Link>
    </div>
  );

  return (
    <div className='row d-flex justify-content-center align-items-center text-center w-100 my-5'>
      <div className='col-12 col-xl-10'>
        <h3 className='my-4'><span className='text-purple'>Категории</span></h3>
        <div className='row w-100 d-flex justify-content-center'>
          {constants.categories.map((categoryProps, index) => (<CategoryCard key={index} {...categoryProps} />))}
        </div>
      </div>
    </div>
  );
};
