import React, { useReducer, useEffect } from 'react';
import { GreyBox } from '../shared/components';
import { useStaticQuery, graphql, Link } from 'gatsby';
import * as JsSearch from "js-search"

const initialState = {
  searchText: '',
  postBundle: null,
  search: null,
  results: null,
}

const reducer = (state, action) => {
  switch (action.type) {
    case 'field':
      return {
        ...state,
        [action.field]: action.value
      }
    default:
      return state;
  }
}


export const SearchBox = () => {
  const data = useStaticQuery(graphql`
    query SearchBox {
      allPosts: allMarkdownRemark(filter: {frontmatter: {tag: {eq: "help"}}}) {
        edges {
          node {
            id
            fields {
              slug
            }
            frontmatter {
              title
              category
            }
            internal {
              content
            }
          }
        }
      }
    }
  `);

  const [state, dispatch] = useReducer(reducer, initialState);
  const { searchText, results, search } = state;


  useEffect(() => {
    dispatch({
      type: 'field',
      field: 'postBundle',
      value: data.allPosts.edges,
    });
    initializeSearch();
  }, []);


  const initializeSearch = () => {
    const dataToSearch = new JsSearch.Search("id");
    const trimmedData = data.allPosts.edges.map(({ node }) => { return node; })
    dataToSearch.indexStrategy = new JsSearch.PrefixIndexStrategy();
    dataToSearch.sanitizer = new JsSearch.LowerCaseSanitizer();

    dataToSearch.searchIndex = new JsSearch.TfIdfSearchIndex("id");
    dataToSearch.addIndex(['frontmatter', 'title']);
    dataToSearch.addIndex(['internal', 'content']);
    dataToSearch.addDocuments(trimmedData);

    dispatch({
      type: 'field',
      field: 'search',
      value: dataToSearch,
    })
  }

  const inputHandler = e => {
    const queryResult = search.search(e.target.value);

    console.log(queryResult);
    dispatch({
      type: 'field',
      field: 'searchText',
      value: e.currentTarget.value
    });
    dispatch({
      type: 'field',
      field: 'results',
      value: queryResult
    });
  }

  return (
    <div className='container my-4'>
      <GreyBox>
        <form className='popup-form'>
          <div className="form-row">
            <div className="form-group input-group col-12 m-0 p-0">
              <div class="input-group-prepend">
                <div class="input-group-text"><span className='icon search'></span></div>
              </div>
              <input
                type="text"
                className="form-control search-bar"
                id="search_text"
                placeholder="Търсете всичко, което ви интересува"
                value={searchText}
                onFocus={(e) => e.target.placeholder = ""}
                onBlur={(e) => e.target.placeholder = "Търсете всичко, което ви интересува"}
                onChange={inputHandler}
              />
            </div>
          </div>
        </form>
        {results && (
          <ul className='category-cards mt-3'>
            {results.map(({ frontmatter, fields }, index) => (
              <li key={index} className='my-2'>
                <Link to={fields.slug}><span className='icon category-arrow'></span>{frontmatter.title}</Link>
                <hr className='normal-bottom' />
              </li>
            ))}
          </ul>
        )}

      </GreyBox>
    </div>
  );
};
